import { useForm } from "react-hook-form";
import { useRecoilState } from "recoil";
import { categoryOptionsState } from "./atoms";

interface IForm {
  category: string;
}

function CreateCategory() {
  const [categories, setCategories] = useRecoilState(categoryOptionsState);
  const {
    register,
    handleSubmit,
    setValue,
    formState: { errors },
  } = useForm<IForm>();
  const onValid = ({ category }: IForm) => {
    setCategories((oldCategories) => [category, ...oldCategories]);
    setValue("category", "");
  };
  return (
    <div>
      <h1 className="text-4xl font-semibold">To Dos</h1>
      <hr />
      <form onSubmit={handleSubmit(onValid)}>
        <input
          className="input input-bordered input-accent w-full max-w-xs"
          {...register("category", {
            required: "Please write a category",
            validate: {
              unique: (v) =>
                !categories.includes(v) || "  .",
            },
          })}
          placeholder="Write a category"
        />
        {errors.category?.message && <span>{errors.category.message}</span>}
        <button className="btn btn-primary">Add</button>
      </form>
    </div>
  );
}

export default CreateCategory;
